* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    font-size: 100%;
    vertical-align: baseline;
    background: transparent;
    box-sizing: border-box;
}




html{
    scroll-behavior: smooth;
}


body{
    font-family: Calibri, Helvetica, sans-serif;
    color: white;  
}


#header{
    z-index: 1;
    position: fixed;
    width: 100%;
    overflow: hidden;
    background-color: black;
}

.title{
    font-size: 5vh;
    font-weight: 700;
    margin-left: 1vh;
    width: auto;
    float: left;  
    color:white; 
}

a{
    text-decoration: none;
}
a:hover{
    text-decoration: underline;
}
.title:hover{
    text-decoration: underline;
    cursor: pointer;
}

.navlistItems {
    font-weight: 600;
    font-size: 3vh;
    margin: 1vh 1vw;
    list-style : none;
    color: white;
}
.navlistItems:hover{
    text-decoration: underline;
    cursor: pointer;
}

.navlist{
    float: right;
    display: flex;
}


#aboutPage{
    min-height: 100vh;
    display: flex;
   /* background-image: url('/static/react/images/About.jpg');*/
   background-image: url('/static/images/About.jpg');
    justify-content: center;
    padding-top: 6vh;
    color: white;
   flex-wrap: wrap;
}

.titleSubpage, .subtitleSubpage{
    color: white;
    font-weight: 700;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.titleSubpage{
    margin-top: 6vh;
    margin-bottom: 2vh;
    font-size:6vh;
    font-size: min(6vh, 6vw);
    animation: wobble 6s linear infinite;
    
}
.subtitleSubpage{
    font-size: 3vh;
    font-size: min(3vh, 3vw);
    font-weight: 700;
    margin-bottom: 4vh;
}




.textSubpage{
    max-width: 1000px;
    font-family: Calibri,sans-serif, Arial;
    letter-spacing: 0.1vw;
    font-size: 2.5vh;
    word-spacing: 0.2vw;
    line-height: 2.5;
    
}


@keyframes wobble{
    0%{
        transform: translateY(0px) rotate(0deg);
    }
    25%{
        transform: translateY(0.5px) rotate(1deg);
    }
    50%{
        transform: translateY(1px) rotate(0deg);
    }
    75%{
        transform: translateY(0.5px) rotate(-1deg);
    }
    100%{
        transform: translateY(0px) rotate(0deg);
    }
}





